div.quill {
  border: none;
  word-wrap: normal;
}
div.quill h1 {
  padding: 0 0 0.2rem;
}
div.quill h2 {
  padding: 0 0 0.5rem;
}
div.quill ul {
  padding-left: 1rem;
}
div.quill ul li::before {
  content: '\25CF';
  padding-right: 0.2rem;
  font-size: 0.7rem;
}
div.quill ul li.ql-indent-1::before {
  content: '\25CB';
}
div.quill ul li.ql-indent-1:not(.ql-direction-rtl) {
  padding-left: 4rem;
}
div.quill ul li.ql-indent-2::before {
  content: '\25CB';
}
div.quill ul li.ql-indent-2:not(.ql-direction-rtl) {
  padding-left: 6.5rem;
}
div.quill ul p::selection {
  color: #ffffff;
  background-color: #3ce458;
}
div.quill ol li:not(.ql-direction-rtl)::before {
  margin-right: 0.75rem;
}
div.quill img {
  width: 100%;
}
div.quill iframe.ql-video {
  width: 100%;
  height: 550px;
}
.ql-clipboard {
  position: fixed;
  left: 50%;
  top: 50%;
}
.ql-container.ql-snow {
  background-color: #e2e2e2;
  margin: 0 auto;
  border: none;
}
.text-editor-content .full-card-options {
  position: fixed;
  z-index: 11;
}
.ql-toolbar {
  text-align: center;
  position: fixed;
  margin: 2.91rem 0 0;
  padding: 0;
  width: 100%;
  z-index: 10;
  background-color: #ececec;
}
.ql-editor {
  font-size: 0.9rem;
  width: 70%;
  margin: 0 auto;
  padding: 8rem 7rem 11rem 6rem;
  border: 0.01rem solid #dfdfdf;
  color: #272727;
  background-color: white;
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
  min-height: 1000px;
}
.ql-editor ::selection {
  background: #ffb7b7;
  /* WebKit/Blink Browsers */
}
.expanded .ql-toolbar {
  margin: 0;
}
.expanded .ql-editor {
  padding: 6rem 7rem 6rem 6rem;
}
.ql-expand:active,
.ql-expand:focus {
  outline: none;
}
span:active,
span:focus {
  outline: none;
}
.expanded > div.quill {
  margin: 0;
}
.expanded > div.quill .ql-expand::before {
  content: '\25BC';
}
.ql-expand::before {
  content: '\25B2';
  border-radius: 0.1rem;
  font-size: 1.1rem;
  color: black;
  line-height: 2px;
  padding: 0.2rem 0.3rem;
  transition-duration: 0.5s;
}
.ql-expand:hover::before {
  content: '\25B2';
  color: #45d458;
}
.ql-delete::before {
  content: '\2145';
  font-size: 1.1rem;
  border-radius: 0.1rem;
  color: black;
  display: inline-block;
  line-height: 2px;
  transition-duration: 0.5s;
}
.ql-delete:hover::before {
  color: #f55656;
}
.ql-exit::before {
  content: '\2715';
  font-size: 1.1rem;
  border-radius: 0.1rem;
  line-height: 2px;
  color: black;
  padding: 0.2rem 0.4rem;
  transition-duration: 0.5s;
}
.ql-exit:hover::before {
  color: #f556db;
}
.ql-snow a {
  color: #45d458;
}
.delete-modal p {
  text-align: center;
  font-size: 1rem;
  padding: 1rem;
}
.delete-modal .ant-modal-content {
  margin: 0;
}
@media only screen and (max-width: 1200px) {
  .ql-editor {
    margin: 0 auto;
    padding: 10rem 5rem 7rem 5rem;
    width: 80%;
  }
  .ql-picker-label {
    font-size: 0.9rem;
  }
  .ql-toolbar.ql-snow .ql-formats {
    margin: 0 0.2rem;
  }
}
@media only screen and (max-width: 1000px) {
  div.quill iframe.ql-video {
    width: 100%;
    height: 200px;
  }
  .expanded .ql-toolbar {
    margin: 0;
  }
  .expanded .ql-editor {
    padding: 7rem 5rem 7rem 4rem;
    margin: 0 auto;
  }
  .text-editor-content .full-card-options h3 {
    width: 18rem;
  }
  .ql-toolbar {
    margin: 2.9rem 0 0;
  }
  .text-editor-content.full-card-options {
    position: fixed;
    z-index: 11;
  }
}
@media only screen and (max-width: 700px) {
  .expanded .ql-editor {
    padding: 8rem 3rem 7rem 2rem;
    margin: 0 auto;
    width: 100%;
  }
}
@media only screen and (max-width: 400px) {
  .text-editor-content .full-card-options h3 {
    width: 55%;
  }
  .ql-editor {
    padding: 11.5rem 1.3rem 7rem 1.3rem;
    margin: 0 auto;
    width: 100%;
  }
  .expanded .ql-editor {
    padding: 9rem 1.3rem 7rem 1.3rem;
    margin: 0 auto;
    width: 100%;
  }
}
