.login-layout {
  height: 100vh;
  margin: 0 auto;
}
.login-layout a {
  color: white;
}
.login-title {
  text-align: center;
  font-size: 1.5rem;
  margin: 0;
  vertical-align: middle;
  color: #41e254;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}
.login-content {
  width: 100%;
  margin: 0 auto;
}
.login-card {
  border: none;
  border-radius: 0;
  padding-top: 30vh;
  height: 100vh;
  width: 100%;
  background-size: cover;
  background: linear-gradient(90deg, #adeeb0 0%, #9feefc 47%, #fff0f1 100%);
}
.login-form {
  display: block;
  margin: 0 auto;
  padding: 1.1rem;
  background-color: #383838;
  box-shadow: 0 0 5px 0 #585858;
  border-radius: 1rem;
  width: 20rem;
  text-align: center;
}
.login-field {
  text-align: center;
  color: black;
  display: block;
  margin: 0.7rem auto;
  width: 83%;
  padding: 0 1rem;
  border-radius: 0.5rem;
}
.login-form div {
  color: white;
}
.login-error {
  color: white;
}
.login-field:focus {
  outline: none;
}
.login-submit {
  display: block;
  background-color: #272727;
  margin: 0 auto;
  width: 70%;
  border-radius: 0.5rem;
  color: #41e254;
  border: none;
}
.login-signup {
  display: block;
  margin: 0.7rem 0 0;
}
a.login-signup:hover {
  color: #41e254;
}
.login-after-card {
  margin-top: 0.7rem;
  width: 100%;
  text-align: center;
}
a.login-forgot-password {
  color: #44bbb5;
}
a.login-forgot-password:hover {
  color: #313131;
}
