.graph-container {
  height: 100vh;
  background-image: url(/static/media/cloud9.22dc8cce.png);
  background-size: cover;
  border: 10px solid #383838;
}
text.graph-text {
  text-align: left;
  fill: #dbdbdb;
  stroke: none;
  transition: 1s;
  fill: white;
  font-size: 1.7rem;
  color: black;
  text-shadow: 0.07em 0 black, 0 0.07em black, -0.07em 0 black, 0 -0.07em black;
}
text.graph-text:hover {
  fill: #b4ffbe;
  transition-duration: 0.1s;
}
.graph-node {
  color: black;
  stroke: #fff019;
  fill: #ffffff;
}
.graph-node:hover {
  fill: #f0ff99;
  stroke-width: 2px;
}
@media only screen and (max-width: 400px) {
  .graph-container {
    background-position-x: center;
  }
}
