.nodelist {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  list-style: none;
  margin: auto;
  width: 100%;
  background-color: transparent !important;
  padding: 0.3rem 0.3rem;
  z-index: -15;
}
.node-card-icon {
  font-size: 5rem;
  color: #b8b8b8;
  display: block;
  text-align: center;
  padding: 3.7rem;
}
li.nodelist-item {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: block;
  text-align: left;
  height: 17rem;
  width: 32.5vw;
  overflow: hidden;
  background-color: #111111;
  margin: 0.25rem 0.25rem;
  color: #757575;
  box-shadow: 0 0 5px 0 #585858;
  border-radius: 0.3rem;
}
.nodelist-options {
  padding: 0.7rem 1.3rem 0.5rem;
  border-bottom: 0.09rem solid #3a3a3a;
}
div.nodelist-options-buttons {
  transition-delay: 0.1s;
  transition-duration: 0.3s;
  opacity: 1;
}
div.nodelist-options-buttons button {
  margin-left: 0.3rem;
  outline: #272727;
  color: white;
  background-color: transparent;
  border-radius: 0.3rem;
  border: none;
  cursor: pointer;
}
div.nodelist-options-buttons button:hover {
  color: #cacaca;
  transition: 0.7s;
}
div.nodelist-options-buttons a {
  margin-left: 0.3rem;
  display: inline-block;
  color: white;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.nodelist-item:hover {
  transition-duration: 1s;
}
.nodelist-item:hover div.nodelist-options-buttons {
  opacity: 1;
  outline: transparent;
}
.nodelist-item p:hover {
  color: #ffffff;
  background: #3a3a3a;
  transition: all 1s ease-out;
}
.nodelist-item-title {
  width: 73%;
  text-align: left;
  color: #98fdc2;
  font-size: 0.97rem;
  transition: all 0.3s ease-out;
  overflow: hidden;
  font-weight: normal;
  margin: 0;
  padding: 0.03rem;
  white-space: nowrap;
  text-overflow: ellipsis;
}
input.nodelist-item-title {
  outline: none;
  color: #b9b9b9;
  background-color: #111111;
  border: none;
  font-size: 0.94rem;
  width: 15.3rem;
  overflow: hidden;
  white-space: normal;
  text-overflow: ellipsis;
}
input.nodelist-item-title::selection {
  background: #111111;
}
.nodelist-item-audio {
  background-image: url(/static/media/cloud9.22dc8cce.png);
  background-position: center;
  background-size: cover;
  height: 11rem;
}
.nodelist-item-audio-full {
  background-image: url(/static/media/cloud9.22dc8cce.png);
  background-position: center;
  background-size: cover;
  height: 82.7vh;
}
.nodelist-item p {
  padding: 0.3rem 1.5rem;
  font-size: 1.07rem;
  color: #e7e7e7;
  min-height: 100%;
  transition: 0.7s;
}
.nodelist-item img {
  background: linear-gradient(90deg, #b7ffcd 0%, #fcfcfc 47%, #9eefff 100%);
  min-height: 100%;
}
.nodelist-collection-grid .node-card-icon {
  padding: 2rem;
  font-size: 3rem;
  border: 0.01rem solid #383838;
  height: 7.7rem;
}
.nodelist-collection-grid p {
  padding: 0.3rem 1.4rem;
  font-size: 0.7rem;
  background-color: #272727;
  border: 0.01rem solid #383838;
}
.nodelist-collection-grid p:hover {
  color: #e7e7e7;
  background-color: #111111;
}
.nodelist-collection-grid {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: row;
          flex-direction: row;
  list-style: none;
  padding: 0;
  margin: 0;
}
.nodelist-collection-grid li {
  width: 50%;
  height: 7.3rem;
  overflow: hidden;
}
.nodelist-collection-grid li.nodelist-collection-grid-full-width {
  width: 100%;
}
.nodelist-collection-item-single {
  height: 100%;
}
@media only screen and (max-width: 1400px) {
  li.nodelist-item {
    width: 32vw;
  }
}
@media only screen and (max-width: 1350px) {
  li.nodelist-item {
    width: 32vw;
  }
  .nodelist-item-title {
    width: 70%;
  }
}
@media only screen and (max-width: 1150px) {
  li.nodelist-item {
    width: 48vw;
  }
  .nodelist-item-title {
    width: 70%;
  }
}
@media only screen and (max-width: 900px) {
  li.nodelist-item {
    width: 48vw;
  }
  .nodelist-item-title {
    width: 70%;
  }
}
@media only screen and (max-width: 700px) {
  li.nodelist-item {
    width: 98vw;
  }
  .nodelist-item-title {
    width: 70%;
  }
}
@media only screen and (max-width: 400px) {
  li.nodelist-item {
    width: 95vw;
  }
  .nodelist-item-title {
    width: 63%;
  }
  div.nodelist-options-buttons button {
    margin-left: 0.01rem;
    margin: 0;
    padding: 0 0.5rem;
  }
}
